.counterInterfaceContainer {
    /*margin-top: 250px;
    margin-left: 550px;*/
    /*display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;*/
    width: 412px;
    height: 250px;
    border: 4px solid aqua;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.integerContainerActive {
    width: 75%;
    height: 55%;
    border: 4px solid aqua;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    font-weight: 900;
    color: aqua;
}

.integerContainerDisabled {
    /*margin-bottom: 10px;*/
    width: 75%;
    height: 55%;
    border: 4px solid aqua;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    font-weight: 900;
    color: #ff0404;
}

.buttonsContainer {
    margin-top: 3%;
    width: 75%;
    height: 20%;
    border: 4px solid aqua;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 10px;
}

.integerContainerTextActive {
    font-size: 25px;
    font-weight: 900;
    color: aqua;
}
.integerContainerTextNegative {
    font-size: 25px;
    font-weight: 900;
    color: #ff001e;
}
