.incBtn {
    width: 110px;
    height: 35px;
    border: 1px solid #fff;
    border-radius: 10px;
    font-size: 25px;
    font-weight: 900;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.56);
    background-color: #00ffee;
    box-shadow: 0 4px 6px rgb(65 132 144 / 10%), 0 1px 3px rgb(0 0 0 / 8%);
    cursor: pointer;
    transition: box-shadow 0.2s;
}

.incBtnDisabled {
    width: 110px;
    height: 35px;
    background-color: #eee;
    color: #444;
    cursor: not-allowed;
    border: 1px solid #fff;
    font-size: 25px;
    font-weight: 900;
    color: rgba(0, 0, 0, 0.56);
    box-shadow: 0 4px 6px rgb(65 132 144 / 10%), 0 1px 3px rgb(0 0 0 / 8%);
    user-select: none;
    appearance: none;
    touch-action: manipulation;
    transition: box-shadow 0.2s;
    border-radius: 10px;
}

